<template>
  <v-data-table
    :headers="headers"
    :items="companyInfo"
    sort-by="razaoSocial"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Transportadoras</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-dialog
          v-model="dialog"
          max-width="700px"
          shaped
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              Novo Cadastro
            </v-btn>
          </template>

          <!-- Company Register Modal Start -->
          <form action="">
            <v-card shaped>
              <v-card-title>{{ formTitle }}</v-card-title>
              
              <v-card-text lazy-validation>  
                <v-text-field
                  v-model="editedItem.cnpj"
                  v-mask="'##.###.###/####-##'"
                  :value="currentValue"
                  @input="handleInput"
                  label="CNPJ"
                  prepend-icon="mdi-card-text"
                  :rules="required"
                  required
                />
                <v-text-field
                  v-model="editedItem.razaoSocial"
                  label="Razão Social"
                  prepend-icon="mdi-clipboard-account"
                  :rules="required"
                  required
                />
                <v-text-field
                  v-model="editedItem.nomeFantasia"
                  label="Nome Fantasia"
                  prepend-icon="mdi-clipboard-account"
                />
                <v-row class="mx-1">
                  <v-select
                    v-model="editedItem.crt"
                    :items="crt"
                    label="CRT"
                  />
                  <v-text-field
                  v-model="editedItem.phoneNumber"
                  label="Número de Celular"
                  v-mask="'(##) # ####-####'"
                  prepend-icon="mdi-cellphone"
                  />
                </v-row>

                <v-row class="mx-1">
                  <v-text-field
                    v-model="cep"
                    name="cep"
                    label="CEP"
                    v-mask="'#####-###'"
                    prepend-icon="mdi-office-building-marker"
                    @keyup="preencherCep"
                  />
                  <v-text-field
                    v-model="numero"
                    label="Número"
                    prepend-icon="mdi-numeric"
                    :rules="required"
                  />
                </v-row>
                <v-text-field
                  v-model="rua"
                  label="Logradouro"
                  name="rua"
                  id="rua"
                />
                <v-text-field
                  v-model="complemento"
                  label="Complemento"
                  :hint="'opcional'"
                  name="complemento"
                  id="complemento"
                />
                <v-row class="mx-1">
                  <v-select
                    v-model="uf"
                    :items="uf"
                    name="uf"
                    id="uf"
                    label="UF"
                    v-mask="'AA'"
                    class="mr-1"
                  />
                  <v-text-field
                    v-model="cidade"
                    label="Cidade"
                    name="cidade"
                    id="cidade"
                  />
                </v-row>
              </v-card-text>
              
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                >
                Cadastrar
                </v-btn>
              </v-card-actions>
            </v-card>
          </form>
        </v-dialog>
        <!-- Company Register Modal End -->

        <!-- Delete Modal -->
        <v-dialog v-model="dialogDelete" max-width="700px">
          <v-card>
            <v-card-title class="text-h6">Desativar Empresa</v-card-title>
            <v-card-text class="red--text">Tem certeza que deseja desativar essa empresa?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDisable">Cancelar</v-btn>
              <v-btn color="blue darken-1" text @click="disableCompanyConfirm">Desativar</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <!-- Delete Modal End -->

      </v-toolbar>
    </template>
    
    <template v-slot:[`item.actions`]="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        class="mr-2"
        @click="disableCompany(item)"
      >
        mdi-delete
      </v-icon>
      <router-link to="/usuarios" class="text-decoration-none">
        <v-icon small> mdi-account-multiple-plus </v-icon>
      </router-link>
    </template>

    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>

<!-- DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR -->

<script>
import axios from 'axios'

  export default {
    name: 'CompanyList',
    data: () => ({
      showPassword: false,
      showPassword2: false,
      dialog: false,
      dialogDelete: false,
      menu: false,
      search: '',
      cep: '',
      rua:'',
      cidade: '',
      complemento: '',
      r: null,
      required: [
        v => !!v || 'Este campo é obrigatório',
      ],
      crt: ['Simples Nacional', 'Lucro Real', 'Lucro Presumido'],
      uf: ['AC', 'AL', 'AM', 'AP', 'BA', 'CE', 'DF', 'ES', 'GO', 'MA', 'MG', 'MS', 'MT', 'PA', 'PB', 'PE', 'PI', 'PR', 'RJ', 'RN', 'RO', 'RR', 'RS', 'SC', 'SE', 'SP', 'TO'],
      headers: [
        {
          text: 'Empresa',
          align: 'start',
          sortable: true,
          value: 'razaoSocial',
        },
        { text: 'CNPJ', value: 'cnpj' },
        { text: 'UF', value: 'uf' },
        { text: '', value: 'actions', sortable: false },
      ],
      companyInfo: [],
      editedIndex: -1,
      editedItem: {
        razaoSocial: '',
        cnpj: '',
        registerDate: '',
        uf: ''
      },
      defaultItem: {
        razaoSocial: '',
        cnpj: '',
        registerDate: '',
        uf: ''
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Cadastrar Empresa' : 'Editar Empresa'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDisable()
      }
    },

    created () {
      this.initialize();
      this.getCep();
    },

    methods: {
      initialize () {
        this.companyInfo = [
          {
            razaoSocial: 'P S DE ALMEIDA',
            cnpj: '80.309.122/0001-01',
            registerDate: '12/05/2019',
            uf: "SP",
          }
        ]
      },

      editItem (item) {
        this.editedIndex = this.companyInfo.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      disableCompany (item) {
        this.editedIndex = this.companyInfo.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      disableCompanyConfirm () {
        this.companyInfo.splice(this.editedIndex, 1)
        this.closeDisable()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDisable () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.companyInfo[this.editedIndex], this.editedItem)
        } else {
          this.companyInfo.push(this.editedItem)
        }
        this.close()
      },

      getCep(cep) {
        return axios.get(`https://viacep.com.br/ws/${cep}/json/`);
      },
      preencherCep() {
        const cep = this.cep.replace(/\D/g, "");
        if(cep.length === 8) {
          this.getCep(cep)
          .then(r => {
            console.log(r);
            this.rua = r.data.logradouro;
            this.uf = r.data.uf;
            this.cidade = r.data.localidade;
            this.complemento = r.data.complemento;
            this.bairro = r.data.bairro;
          })
        }
      },
    },
  }
</script>